<h1 id="ubuntu安装根证书" tabindex="-1"><a class="header-anchor" href="#ubuntu安装根证书" aria-hidden="true">#</a> Ubuntu安装根证书</h1>
<h2 id="场景说明" tabindex="-1"><a class="header-anchor" href="#场景说明" aria-hidden="true">#</a> 场景说明</h2>
<p>抓https的包需要安装证书</p>
<h2 id="首先要创建证书" tabindex="-1"><a class="header-anchor" href="#首先要创建证书" aria-hidden="true">#</a> 首先要创建证书</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">mkdir</span> /usr/share/ca-certificates/extra
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">cp</span> foo.crt /usr/share/ca-certificates/extra/foo.crt
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> dpkg-reconfigure ca-certificates
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> update-ca-certificates
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="将-pem证书转换为-crt格式" tabindex="-1"><a class="header-anchor" href="#将-pem证书转换为-crt格式" aria-hidden="true">#</a> 将.pem证书转换为.crt格式</h2>
<p>直接改后缀名即可或者使用ssl工具</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>openssl x509 -in foo.pem -inform PEM -out foo.crt
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>